import {
	wxLogin
} from '@/api/login.js';
import {
	setToken,
	setUserInfo
} from '@/utils/auth.js';
import {
	goPage
} from '@/utils/index.js';
import {
	mapState
} from 'vuex';
export default {
	computed: {
		...mapState(['routerWait'])
	},
	methods: {
		register() {
			uni.navigateTo({
				url: '/pages/register/register'
			});
		},
		phoneLogin() {
			uni.navigateTo({
				url: '/pages/phoneLogin/phoneLogin'
			})
		},
		wxLogin(e) {

			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					console.log({
						code: loginRes.code,
						encryptedData: e.detail.encryptedData,
						errMsg: e.detail.errMsg,
						iv: e.detail.iv,
						rawData: e.detail.rawData,
						signature: e.detail.signature,
						userInfo: e.detail.userInfo
					});
					// 微信登录
					wxLogin({
						code: loginRes.code,
						encryptedData: e.detail.encryptedData,
						errMsg: e.detail.errMsg,
						iv: e.detail.iv,
						rawData: e.detail.rawData,
						signature: e.detail.signature,
						userInfo: e.detail.userInfo
					}).then(res => {
						if (res.code === 200) {
							setToken(res.data.token);
							setUserInfo(res.data.userInfo);
							if (this.routerWait.routerPath) {
								goPage(this.routerWait.routerType, this.routerWait.routerPath);
							}
							uni.showToast({
								icon: 'none',
								title: '登录成功'
							})
						} else {
							uni.showToast({
								icon: 'none',
								title: res.message
							})
						}
					})
				}
			});
		}
	}
}
